:root {
  --color-darkest: #343a40;
  --color-dark: #495057;
  --color-medium: #cdcecf;
  --color-light: #e2e2e2;
  --color-lightest: #f1f3f5;

  --color-theme: #ffdb64;
  --color-danger: #ff4d4d;
  --color-success: #089666;
}

/*@import url("https://fonts.googleapis.com/css2?family=Codystar&display=swap");*/
@import url("https://fonts.googleapis.com/css2?family=Vazirmatn:wght@100..900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Vazirmatn", sans-serif;
}

body {
  min-height: 100vh;
  color: var(--color-darkest);
  background-color: var(--color-lightest);
  padding: 3.2rem;
  direction: rtl;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main {
  width: 50rem;
  margin: auto;
}

.app-header {
  width: 66rem;
  margin: 1rem auto 4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.error {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 500;
  padding: 2rem;
  background-color: #495057;
  border-radius: 100px;
}

img {
  width: 14rem;
}

h1 {
  font-size: 5.2rem;
}

h2 {
  font-size: 3.6rem;
  margin-bottom: 2rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 600;
  margin-bottom: 4rem;
}

h4 {
  font-size: 2.2rem;
  font-weight: 600;
  margin-bottom: 2.4rem;
}

.start {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress {
  margin-bottom: 4rem;
  display: grid;
  justify-content: space-between;
  gap: 1.2rem;
  grid-template-columns: auto auto;
  font-size: 1.8rem;
  color: var(--color-dark);
}

progress {
  -webkit-appearance: none;
  width: 100%;
  height: 12px;
  grid-column: 1 / -1;
  border-radius: 100px;
}

::-webkit-progress-bar {
  background-color: var(--color-dark);
  border-radius: 100px;
}
::-webkit-progress-value {
  background-color: var(--color-theme);
  border-radius: 100px;
}
::-moz-progress-bar {
  background-color: var(--color-dark);
  border-radius: 100px;
}
::-moz-progress-value {
  background-color: var(--color-success);
  border-radius: 100px;
}
.btn {
  display: block;
  font-family: inherit;
  color: var(--color-darkest);
  font-size: 2.2rem;
  border: 2px solid var(--color-theme);
  background-color: var(--color-theme);
  padding: 1.2rem 2.4rem;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s;
}
.btn-option {
  font-size: 2rem;
  color: var(--color-dark);
  border: 2px solid var(--color-light);
  background-color: var(--color-light);
}
.btn:not([disabled]):hover {
  color: var(--color-darkest);
  background-color: var(--color-theme);
  border: 2px solid var(--color-dark);
}

.btn-option:not([disabled]):hover {
  transform: translateX(1.2rem);
  color: var(--color-darkest);
  border: 2px solid var(--color-theme);
  background-color: var(--color-theme);
}

.btn[disabled]:hover {
  cursor: not-allowed;
}

.btn-ui {
  float: left;
  margin-top: 0.5rem;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin-bottom: 3.2rem;
}

.btn-option {
  width: 100%;
  text-align: right;
  margin-bottom: 0.5rem;
}

.btn-option.correct {
  background-color: var(--color-success);
  color: var(--color-light);
}

.answer {
  transform: translateX(2rem);
  background-color: var(--color-medium);
}
.answer.wrong {
  color: var(--color-danger);
}
.result {
  background-color: var(--color-success);
  color: var(--color-lightest);
  border-radius: 100px;
  text-align: center;
  padding: 2rem 0;
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1.6rem;
}

.result span {
  font-size: 2.2rem;
  margin-right: 4px;
}

.highscore {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 4.8rem;
}

.loader-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4rem;
  gap: 1.6rem;

  color: var(--color-medium);
  font-size: 1.4rem;
}

.timer {
  float: left;
  font-size: 1.8rem;
  color: var(--color-medium);
  border: 2px solid var(--color-dark);
  padding: 1.35rem 2.8rem;
  border-radius: 100px;
}

/* CREDIT: https://dev.to/afif/i-made-100-css-loaders-for-your-next-project-4eje */
.loader {
  width: 50px;
  height: 24px;
  background: radial-gradient(circle closest-side, currentColor 90%, #0000) 0%
      50%,
    radial-gradient(circle closest-side, currentColor 90%, #0000) 50% 50%,
    radial-gradient(circle closest-side, currentColor 90%, #0000) 100% 50%;
  background-size: calc(100% / 3) 12px;
  background-repeat: no-repeat;
  animation: loader 1s infinite linear;
}

@keyframes loader {
  20% {
    background-position: 0% 0%, 50% 50%, 100% 50%;
  }
  40% {
    background-position: 0% 100%, 50% 0%, 100% 50%;
  }
  60% {
    background-position: 0% 50%, 50% 100%, 100% 0%;
  }
  80% {
    background-position: 0% 50%, 50% 50%, 100% 100%;
  }
}

/* ********** */
/* First counter example */
.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  font-size: 2rem;
  font-weight: bold;
  margin: 6rem;
}

.counter * {
  font-size: inherit;
  padding: 0.8rem;
}
